import React from 'react'
import axios from 'axios';
import { useSelector } from 'react-redux';
const CheckoutStripe = ({products , userData }) => {
    const token = useSelector(state => state.cart.token)
    const paymentHandler  = async() => {
        console.log("products" , products)
        console.log("userData" , userData)
        console.log("token" , token)

        const res = await axios.post('https://e-commerce-hh3m.onrender.com/api/create-checkout-session', {
                products
                }, {
                headers: {
                    "Authorization": token &&`bearer ${token}`
                }
            }).then((res) => {
                if (res.data.url) {
                    window.location.href = res.data.url
                }
            }).catch((error) => {
                console.log(error)
            })
    }


    return (
        <div className='stripe'>
                <button type="submit" className='btn btn-danger' onClick={paymentHandler}>Checkout</button>
        </div>
    )
}

export default CheckoutStripe