import { AiFillCheckCircle } from "react-icons/ai";
import { Link } from "react-router-dom";

const CheckoutSuccess = () => {
    return (
        <div class="successPage  d-flex flex-column justify-content-center align-items-center">
            <div className="successIcon">
                <AiFillCheckCircle />
            </div>
            <div className="successText text-center">
                <h1>Thank You!</h1>
                <p>Payment Done Successfully</p>
                <p>You Will be redirected to the home page shortly<br />
                    or click here to return to home page
                </p>
            </div>
            <div className="homePageButton">
                <Link to='/'><button>Home Page</button></Link>
            </div>
        </div>
    )
}

export default CheckoutSuccess