import './App.css';
import Loader from './Components/Loaders/Loader';
import React , {Suspense, lazy} from 'react';
import { Routes } from 'react-router-dom';
import { Route } from 'react-router-dom';
import Layout from './Components/Layouts/Layout';
import { LogOut } from './Components/RTK/Slices/CartSlice';
import AdminLayout from './Components/Layouts/AdminLayout';
import { useDispatch, useSelector } from 'react-redux';
import CheckoutSuccess from './Components/checkout/CheckoutSuccess';
import CheckoutFail from './Components/checkout/CheckoutFail';

const ProductDetails = lazy(() => import ('./pages/ProductDetails'))
const StoreContent = lazy(() => import ('./Components/StoreContent/StoreContent'))
const Signup = lazy(() => import ('./pages/Signup'))
const Store = lazy(() => import ('./pages/Store'))
const PageNotFound = lazy(() => import ('./pages/PageNotFound'))
const Contact = lazy(() => import ('./pages/Contact'))
const Login = lazy(() => import ('./pages/Login'))
const DashboardAddProduct = lazy(() => import ('./Components/AdminDashboard/DashboardAddProduct'))
const DashboardProduct = lazy(() => import ('./Components/AdminDashboard/DashboardProduct'))
const Profile = lazy(() => import ('./pages/Profile'))
const ProductCustomization = lazy(() => import ('./Components/AdminDashboard/ProductCustomization'))
const DashboardHome = lazy(() => import ('./Components/AdminDashboard/DashboardHome'))
const Home = lazy(() => import ('./pages/Home'))
const DashboardOrders = lazy(() => import ('./Components/AdminDashboard/DashboardOrders'))
const StatusOrder = lazy(() => import ('./Components/AdminDashboard/dashStatusOrder/StatusOrder'))
const ProductsOrder = lazy(() => import ('./Components/AdminDashboard/orderDetails/ProductsOrder'))
const OrderInformation = lazy(() => import ('./Components/AdminDashboard/orderDetails/OrderInformation'))
const DashboardUsers = lazy(() => import ('./Components/AdminDashboard/DashboardUsers'))

function App() {
  // 
  const isLogin = useSelector(state => state.cart.isLogin)
  const isAdmin = useSelector(state => state.cart.isAdmin)
  const dispatch = useDispatch()
  setTimeout(() => {
    dispatch(LogOut())
    window.localStorage.clear()
  }, 1000 * 60 * 60);

  return (
    <div className="App position-relative overflow-hidden ">
      <Suspense fallback={<Loader />}>
      <Routes>
        <Route path='/' element={<Layout />} >

          <Route index element={<Home />} />
          <Route path='e-commerce' element={<Home />} />
          <Route path='products/:category' element={<StoreContent />} />
          <Route path='productDetails/:id' element={<ProductDetails />} />
          {!isLogin && <Route path='login' element={<Login />} />}
          <Route path='signup' element={<Signup />} />
          <Route path='store' element={<Store />} />
          <Route path='products/:category' element={<Store />} />
          <Route path='store/search' element={<Store />} />
          <Route path='store/search/:search' element={<Store />} />
          <Route path="*" element={<PageNotFound />} />
          <Route path='contact' element={<Contact />} />
          {isLogin &&<Route path='profile' element={<Profile />} />}
          <Route path='checkout-success' element={<CheckoutSuccess />} />
          <Route path='checkout-fail' element={<CheckoutFail />} />

          {isLogin && isAdmin && <Route path='admin' element={<AdminLayout />}>
            <Route path='home' element={<DashboardHome />} /> 
            <Route path='products' element={<DashboardProduct />} />
            <Route path='products/:id' element={<ProductCustomization />} />
            <Route path='addProducts' element={<DashboardAddProduct />} />
            <Route path='orders' element={<DashboardOrders />} />
            <Route path='orders/:id' element={<StatusOrder />} />
            <Route path='orders/products/:id' element={<ProductsOrder />} />
            <Route path='orders/information/:id' element={<OrderInformation />} />
            <Route path='orders/users' element={<DashboardUsers />} />
          </Route>}
        </Route>
      </Routes>
      </Suspense>
    </div>
  );
}

export default App;
