import { Link } from 'react-router-dom'
import { AiFillFrown } from "react-icons/ai";

const CheckoutFail = () => {
    return (
    <div class="successPage  d-flex flex-column justify-content-center align-items-center">
    <div className="failIcon btn-danger">
        <AiFillFrown />
    </div>
    <div className="failText text-secondary text-center">
        <h1 className='text-danger'>Payment Failed</h1>
        <p>unfortionatly your payment failed</p>
        <p>You Will be redirected to the home page shortly<br />
            or click here to return to home page
        </p>
    </div>
    <div className="homeButton">
        <Link to='/'><button className='btn btn-primary'>Home Page</button></Link>
    </div>
</div>
    )
}

export default CheckoutFail